import { Component } from '@angular/core';

import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { ServicesLoginProvider } from '../../providers/services-login/services-login';
import { Validators, FormControl, FormGroup } from '@angular/forms';

import { Geolocation } from '@ionic-native/geolocation';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileTransferObject, FileUploadOptions } from '@ionic-native/file-transfer';

import { File } from '@ionic-native/file';

/**
 * Generated class for the InfoClientePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-info-cliente',
  templateUrl: 'info-cliente.html',
})
export class InfoClientePage {
  pet: string = "pagos";

  id = 0;
  idVendedor = 0;
  ventas = [];
  suma = 0;
  cliente:string;

  vta:number;

  comprados = [];
  noComprados = [];
  faltantes = [];

  formaInforme:FormGroup;

  myPhoto:any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private _login:ServicesLoginProvider,
    private geolocation: Geolocation,
    private camera: Camera,
    private transfer: FileTransfer,
    private file: File,
    private loadingCtrl:LoadingController
  ) {

    this.geolocation.getCurrentPosition().then((resp) => {
     // resp.coords.latitude
     // resp.coords.longitude
    }).catch((error) => {
      console.log('Error getting location', error);
    });

    let watch = this.geolocation.watchPosition();
    watch.subscribe((data) => {
      console.log('latitude : ', data.coords.latitude);
      console.log('longitude : ', data.coords.longitude );
     // data can be a set of coordinates, or an error (if an error occurred).
     // data.coords.latitude
     // data.coords.longitude
    });

    console.log( this.navParams.get('id'));
    this.id = this.navParams.get('id');

    // let dataStorage =  JSON.parse(localStorage.getItem('userData'))
    // this.idVendedor = dataStorage.id;
    //

    this.formaInforme = new FormGroup({
      instrumento_legal: new FormControl('', Validators.required),
      nomenclatura_catastral: new FormControl('', Validators.required),
      lote: new FormControl('', Validators.required),
      guia: new FormControl('', Validators.required),
      cuit_titular: new FormControl('', [Validators.required, Validators.pattern('^[0-9\-\+]{11}$')]),
      cuit_obrajero: new FormControl('', [Validators.required, Validators.pattern('^[0-9\-\+]{11}$')]),
      vol: new FormControl('', Validators.required),
      rodal: new FormControl('', Validators.required),
      tendida: new FormControl('', Validators.required),
      rollo: new FormControl('', Validators.required),
      especie: new FormControl(''),
      gps: new FormControl('', Validators.required),
      foto: new FormControl('', Validators.required),
      diametro_1: new FormControl('', Validators.required),
      diametro_2: new FormControl('', Validators.required),
      largo: new FormControl('', Validators.required),
      volumen: new FormControl('', Validators.required),
      gps_transporte: new FormControl('', Validators.required),
      foto_transporte: new FormControl('', Validators.required),
    })

    this._login.getProveedores()
        .subscribe( (resp3:any)=>{
          console.log(resp3)
          for(let proveedor of resp3) {
            this.noComprados.push({
              id:proveedor.codigo,
              cant:null,
              nombre:proveedor.razon
            })
          }

          console.log(this.noComprados)
        })

    this._login.getCliente(this.id)
        .subscribe( (resp:any)=>{
          console.log(resp[0].razon)
          this.cliente = resp[0].razon;
        })

    this._login.getNotas(this.id)
        .subscribe( (resp:any)=>{
          this.ventas = resp;
          for (let venta of this.ventas) {
            // console.log(venta.codmov)
            // console.log(parseInt(venta.codmov))

            parseInt(venta.codmov) >= 6 ? this.suma = this.suma + parseFloat(venta.importe) : this.suma = this.suma - parseFloat(venta.importe);
          }
        })

        this._login.getProveedoresComprados(this.id)
            .subscribe( (resp:any)=>{
              console.log(resp);


              console.log(resp.length);

              if(resp.length > 0){
                for (var i = 1; i < 51; i ++) {

                  let proveedor_id = resp[0]["PROVE"+i];
                  let proveedor_cant = resp[0]["COMPR"+i];
                  let proveedor_name:string;

                  if(resp[0]["PROVE"+i] != 0){

                    this._login.getProveedor(resp[0]["PROVE"+i])
                        .subscribe( (resp2:any)=>{
                          console.log(resp2);

                          console.log(resp2.length);
                          resp2.length ? proveedor_name = resp2[0].razon : proveedor_name = "Sin definir";

                          this.comprados.push({
                            id:proveedor_id,
                            cant:proveedor_cant,
                            nombre:proveedor_name
                          })

                        })

                        let index = this.noComprados.findIndex(x => x.id === proveedor_id);

                        if (index > -1) {
                          this.noComprados.splice(index, 1);
                        }

                  }
                }
              }else{
                console.log("La query de comprados viene vacia");
                this.comprados.push({
                  id:null,
                  cant:null,
                  nombre:'No se encuentra registros'
                })

              }
            })

        this._login.getProveedoresFaltantes(this.id)
            .subscribe( (resp:any)=>{
              console.log(resp);


              console.log(resp.length);

              if(resp.length > 0){
                console.log("La query de faltantes viene llena");
                console.log(resp);

                for (var i = 1; i < 51; i ++) {
                  console.log("entra al for");
                  let proveedor_id = resp[0]["PROVE"+i];
                  let proveedor_cant = resp[0]["CANT"+i];
                  let proveedor_name:string;

                  if(resp[0]["PROVE"+i] != 0){

                    this._login.getProveedor(resp[0]["PROVE"+i])
                        .subscribe( (resp2:any)=>{
                          console.log(resp2);

                          console.log(resp2.length);
                          resp2.length ? proveedor_name = resp2[0].razon : proveedor_name = "Sin definir";

                          this.faltantes.push({
                            id:proveedor_id,
                            cant:proveedor_cant,
                            nombre:proveedor_name
                          })

                        })

                  }
                }
              }else{
                console.log("La query de faltantes viene vacia");
                this.comprados.push({
                  id:null,
                  cant:null,
                  nombre:'No se encuentra registros'
                })

              }
            })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad InfoClientePage');
  }

  takePhoto(){
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.FILE_URI,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE
    }

    this.camera.getPicture(options).then((imageData) => {
     // imageData is either a base64 encoded string or a file URI
     // If it's base64 (DATA_URL):
     // this.myPhoto = 'data:image/jpeg;base64,' + imageData;
     alert(imageData)
     this.myPhoto = imageData;
    }, (err) => {
     // Handle error
    });
  }

  uploadPhoto(){
    //Show loading
   let loader = this.loadingCtrl.create({
     content: "Uploading..."
   });
   loader.present();

   //create file transfer object
   const fileTransfer: FileTransferObject = this.transfer.create();

   //random int
   var random = Math.floor(Math.random() * 100);

   //option transfer
   let options: FileUploadOptions = {
     fileKey: 'photo',
     fileName: "myImage_" + random + ".jpg",
     chunkedMode: false,
     httpMethod: 'post',
     mimeType: "image/jpeg",
     headers: {}
   }

   //file transfer action
   fileTransfer.upload(this.myPhoto, 'http://appsausol.com.ar.elserver.com/forestal/subeFoto.php', options)
     .then((data) => {
       alert(data);
       alert("Success");
       loader.dismiss();
     }, (err) => {
       alert('error: '+err);
       alert("Error");
       loader.dismiss();
     });
  }

}



